main {
  width: 100vw;
  height: 100vh;
}

#bg-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

#slogan {
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  color: #fff;
  font-size: 32px;
  font-weight: 600;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
}

/* Animation */
p {
  animation: animated-text 2s steps(30, end) 1s 1 normal both;
}

/* text animation */
@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 512px;
  }
}
